import {
  CSSProperties,
  FC,
  forwardRef,
  PropsWithChildren,
  useState,
} from "react";
import styles from "./WordSelector.module.scss";
import { Input, Preloader } from "../../components";
import { AddCircle, Trash } from "../../assets/icons";
import { DndProvider } from "react-dnd";
import { TouchBackend } from "react-dnd-touch-backend";
import { HTML5Backend } from "react-dnd-html5-backend";
import { withDrag, withDrop } from "../../hocs/";
import cx from "classnames";
import { isTouchDevice } from "../../utils";

const dndProviderBackend = isTouchDevice() ? TouchBackend : HTML5Backend;

enum WordType {
  Unused = "unused",
  Included = "included",
  Excluded = "excluded",
}

interface CSSProps {
  className?: string;
  style?: CSSProperties;
}

const WordTile = forwardRef<
  HTMLDivElement,
  { style?: CSSProperties; word: string; onDeleteClicked?: () => void }
>((props, ref) => {
  return (
    <div
      ref={ref}
      className={styles.word}
      style={props.style}
      title={props.word}
    >
      <div>{props.word}</div>
      {props.onDeleteClicked && <Trash onClick={props.onDeleteClicked} />}
    </div>
  );
});

const WordsArea = forwardRef<HTMLDivElement, PropsWithChildren<CSSProps>>(
  ({ className, style, children }, ref) => {
    return (
      <div ref={ref} className={className} style={style}>
        {children}
      </div>
    );
  }
);

const DraggableWordTile = withDrag(WordTile);

const DroppableWordsArea = withDrop(WordsArea);

type Words = {
  unused?: string[];
  included?: string[];
  excluded?: string[];
};

interface Props extends CSSProps {
  words?: Readonly<Words>;
  onWordStatusChanged?: (words: Words) => void;
  status?: "loading" | "succeeded" | "failed";
}

export const WordSelector: FC<Props> = ({
  className,
  style,
  words,
  onWordStatusChanged,
  status,
}) => {
  const isLoaded = status === "succeeded" || status === "failed";

  const unusedWords = words?.unused ?? [];
  const includedWords = words?.included ?? [];
  const excludedWords = words?.excluded ?? [];

  const [includedWord, setIncludedWord] = useState("");
  const [excludedWord, setExcludedWord] = useState("");

  const onAddIncludedWordClicked = () => {
    onWordStatusChanged?.call(null, {
      included: [includedWord, ...includedWords],
    });
    setIncludedWord("");
  };

  const onAddExcludedWordClicked = () => {
    onWordStatusChanged?.call(null, {
      excluded: [excludedWord, ...excludedWords],
    });
    setExcludedWord("");
  };

  const onDeleteWordClicked = (
    type: WordType.Included | WordType.Excluded,
    index: number
  ) => {
    const words = {
      included: includedWords,
      excluded: excludedWords,
    };

    onWordStatusChanged?.call(null, {
      unused: [words[type][index]!, ...unusedWords],
      [type]: words[type].filter((_, i) => i !== index),
    });
  };

  const onWordDropped = (
    areaType: WordType,
    word: { id: string; type: WordType }
  ) => {
    const words = {
      unused: unusedWords,
      included: includedWords,
      excluded: excludedWords,
    };
    const wordIndex = Number(word.id);
    onWordStatusChanged?.call(null, {
      [areaType]: [words[word.type][wordIndex], ...words[areaType]],
      [word.type]: words[word.type].filter((_, i) => i !== wordIndex),
    });
  };

  return (
    <DndProvider backend={dndProviderBackend}>
      <div className={cx(styles.wordSelector, className)} style={style}>
        <DroppableWordsArea
          className={cx(styles.wordsArea, styles.wordsAreaBig)}
          accept={isLoaded ? [WordType.Included, WordType.Excluded] : ["", ""]}
          onDropped={(item) => onWordDropped(WordType.Unused, item)}
        >
          {status === "loading" && <Preloader className={styles.preloader} />}
          {status === "succeeded" &&
            (unusedWords.length ? (
              <div className={styles.wordsWrapper}>
                <div className={styles.words}>
                  {unusedWords.map((word, i) => (
                    <DraggableWordTile
                      id={String(i)}
                      type={WordType.Unused}
                      word={word}
                      key={i}
                    />
                  ))}
                </div>
              </div>
            ) : (
              <div className={styles.message}>No suggestions found</div>
            ))}
          {status === "failed" && (
            <div className={styles.message}>Failed to fetch suggestions :(</div>
          )}
        </DroppableWordsArea>
        <div>
          <DroppableWordsArea
            className={cx(styles.wordsArea, styles.wordsAreaSmall)}
            accept={[WordType.Unused, WordType.Excluded]}
            onDropped={(item) => onWordDropped(WordType.Included, item)}
          >
            {includedWords.length ? (
              <div className={styles.wordsWrapper}>
                <div className={styles.words}>
                  {includedWords.map((word, i) => (
                    <DraggableWordTile
                      style={{ borderColor: "#41b167" }}
                      id={String(i)}
                      type={WordType.Included}
                      word={word}
                      onDeleteClicked={
                        isLoaded
                          ? () => onDeleteWordClicked(WordType.Included, i)
                          : undefined
                      }
                      key={i}
                    />
                  ))}
                </div>
              </div>
            ) : (
              <div className={styles.message}>
                Drag and drop words you
                <br /> want to limit the search to
                <br /> into this box
              </div>
            )}
          </DroppableWordsArea>
          <form
            className={styles.row}
            onSubmit={(e) => {
              e.preventDefault();
              onAddIncludedWordClicked();
            }}
          >
            <Input
              className={styles.input}
              value={includedWord}
              changeHandler={(value) => setIncludedWord(value)}
              placeholder="Or write your own words to add"
              maxLength={60}
            />
            <button
              className={styles.plusButton}
              type="submit"
              disabled={!includedWord}
            >
              <AddCircle size={20} opacity={includedWord ? 1 : 0.25} />
            </button>
          </form>
        </div>
        <div>
          <DroppableWordsArea
            className={cx(styles.wordsArea, styles.wordsAreaSmall)}
            accept={[WordType.Unused, WordType.Included]}
            onDropped={(item) => onWordDropped(WordType.Excluded, item)}
          >
            {excludedWords.length ? (
              <div className={styles.wordsWrapper}>
                <div className={styles.words}>
                  {excludedWords.map((word, i) => (
                    <DraggableWordTile
                      style={{ borderColor: "#ef336a" }}
                      id={String(i)}
                      type={WordType.Excluded}
                      word={word}
                      onDeleteClicked={
                        isLoaded
                          ? () => onDeleteWordClicked(WordType.Excluded, i)
                          : undefined
                      }
                      key={i}
                    />
                  ))}
                </div>
              </div>
            ) : (
              <div className={styles.message}>
                drag and drop words you
                <br /> want to remove from your
                <br /> search into this box
              </div>
            )}
          </DroppableWordsArea>
          <form
            className={styles.row}
            onSubmit={(e) => {
              e.preventDefault();
              onAddExcludedWordClicked();
            }}
          >
            <Input
              className={styles.input}
              value={excludedWord}
              changeHandler={(value) => setExcludedWord(value)}
              placeholder="Or write your own words to remove"
              maxLength={60}
            />
            <button
              className={styles.plusButton}
              type="submit"
              disabled={!excludedWord}
            >
              <AddCircle size={20} opacity={excludedWord ? 1 : 0.25} />
            </button>
          </form>
        </div>
      </div>
    </DndProvider>
  );
};
