import {
  useContext,
  useState,
  useRef,
  useEffect,
  useMemo,
  useCallback,
} from "react";
import styles from "./Search.module.scss";
import { Input, Select, Preloader, Label, BasicModal } from "../../components";
import { SearchNameModal } from "./SearchNameModal";
import { WordSelector } from "./WordSelector";
import { SearchTable } from "./SearchTable";
import { Edit, Trash, Triangle, Warning } from "../../assets/icons";
import context from "../../context";
import { connect, useSelector, useDispatch } from "react-redux";
import { selectCompanySubscriptionPlan } from "../../features/subscriptionPlans/subscriptionPlansSlice";
import {
  selectAllMarketsAsInputOptions,
  fetchAllMarkets,
} from "../markets/marketsSlice";
import {
  selectAllIndustriesAsInputOptions,
  fetchAllIndustries,
} from "../industries/industriesSlice";
import * as actions from "../../store/actions";
import { useHistory, useParams } from "react-router-dom";
import { notify } from "react-notify-toast";
import { useTemporaryErrors } from "../../hooks";
import {
  fetchKeywordSuggestions,
  fetchMentions,
  calculateShareOfSearchWidget,
} from "../../store/api";
import cx from "classnames";
import { useImmer } from "use-immer";
import set from "lodash/set";
import update from "lodash/update";
import { withError } from "../../hocs";

const InputWithError = withError(Input);
const SelectWithError = withError(Select);

type Suggestions = {
  [brandName: string]:
    | {
        [marketCode: string]:
          | {
              loaded?: {
                status: "loading" | "succeeded" | "failed";
                data?: Awaited<ReturnType<typeof fetchKeywordSuggestions>>;
              };
              unused?: string[];
              included?: string[];
              excluded?: string[];
              selected?: { [suggestion: string]: true };
            }
          | undefined;
      }
    | undefined;
};

type Mentions = {
  [brandName: string]: {
    [marketCode: string]: {
      loaded?: {
        status: "loading" | "succeeded" | "failed";
        data?: Awaited<ReturnType<typeof fetchMentions>>;
      };
      removed?: { [id: string]: true };
    };
  };
};

interface Props {
  userId: Store.User["id"];
  companyMembers: Store.Company["members"];
  searches: Store.Searches;
  createSearch: typeof actions.createSearch;
  editSearch: typeof actions.editSearch;
  removeSearch: typeof actions.removeSearch;
  setCurrent: typeof actions.setCurrent;
}

const Search = ({
  userId,
  companyMembers,
  searches: initialSearches,
  createSearch,
  editSearch,
  removeSearch,
}: Props) => {
  // GENERAL
  const companySubscriptionPlan = useSelector(selectCompanySubscriptionPlan);
  const marketsStatus = useSelector(({ markets }: RootState) => markets.status);
  const marketOptions = useSelector(selectAllMarketsAsInputOptions);
  const industriesStatus = useSelector(
    ({ industries }: RootState) => industries.status
  );
  const industryOptions = useSelector(selectAllIndustriesAsInputOptions);

  const dispatch = useDispatch();

  const history = useHistory();

  const { setIsGlobalPreloaderShown, setModalElement } = useContext(context);

  const { errors, setErrors } = useTemporaryErrors(3000);

  // CURRENT SEARCH
  const { id: passedSearchId } = useParams<{ id: string }>();

  const passedSearch = useMemo(() => {
    if (passedSearchId === "new") return;

    return initialSearches.find(({ id }) => id === passedSearchId);
  }, [passedSearchId, initialSearches]);

  const isNewSearch = !passedSearch;

  const urlBasedValues = useMemo(() => {
    const search = history.location.search;
    const params = new URLSearchParams(search);

    const stage = Number(params.get("stage"));

    return {
      stage: (stage === 1 || stage === 2 ? stage : 1) as 1 | 2,
      mainBrand: params.get("mainBrand") ?? "",
      competitor1: params.get("competitor1") ?? "",
      competitor2: params.get("competitor2") ?? "",
      competitor3: params.get("competitor3") ?? "",
      competitor4: params.get("competitor4") ?? "",
      marketCode: params.get("marketCode") ?? "",
      industryId: params.get("industryId") ?? "",
    };
  }, [history.location.search]);

  const [mainBrand, setMainBrand] = useState(
    passedSearch?.brands.main ?? urlBasedValues.mainBrand
  );
  const [competitor1, setCompetitor1] = useState(
    passedSearch?.brands.competitors[0] ?? urlBasedValues.competitor1
  );
  const [competitor2, setCompetitor2] = useState(
    passedSearch?.brands.competitors[1] ?? urlBasedValues.competitor2
  );
  const [competitor3, setCompetitor3] = useState(
    passedSearch?.brands.competitors[2] ?? urlBasedValues.competitor3
  );
  const [competitor4, setCompetitor4] = useState(
    passedSearch?.brands.competitors[3] ?? urlBasedValues.competitor4
  );
  const [marketCode, setMarketCode] = useState(
    passedSearch?.market.code ?? urlBasedValues.marketCode
  );
  const [industryId, setIndustryId] = useState(
    passedSearch?.industry.id ?? urlBasedValues.industryId
  );

  const [stage, setStage] = useState<1 | 2>(() => {
    if (urlBasedValues.stage === 2) {
      const errors = validate();
      if (Object.keys(errors).length) {
        setErrors(errors);
        return 1;
      }
      return 2;
    }
    return 1;
  });

  const [
    isSentimentSourcesDropdownOpen,
    setIsSentimentSourcesDropdownOpen,
  ] = useState(false);
  const [
    isSearchSourcesDropdownOpen,
    setIsSearchSourcesDropdownOpen,
  ] = useState(false);

  const nextButtonRef = useRef<HTMLButtonElement>(null);

  const filledInBrands = useMemo(() => {
    return [
      mainBrand,
      ...[competitor1, competitor2, competitor3, competitor4].filter(
        (value) => value
      ),
    ];
  }, [mainBrand, competitor1, competitor2, competitor3, competitor4]);

  const [
    currentSentimentRefinerBrand,
    setCurrentSentimentRefinerBrand,
  ] = useState("");

  const [currentSearchRefinerBrand, setCurrentSearchRefinerBrand] = useState(
    ""
  );

  const [suggestions, updateSuggestions] = useImmer<Suggestions>(() => {
    const passedSuggestions: Suggestions = {};

    Object.entries(passedSearch?.suggestions || {}).forEach(
      ([brandName, values]) => {
        set(passedSuggestions, [brandName, marketCode], {
          ...values,
          selected: values?.selected?.reduce((acc, suggestion) => {
            acc[suggestion] = true;
            return acc;
          }, {} as { [suggestion: string]: true }),
        });
      }
    );

    return passedSuggestions;
  });

  const wordSelectorData = useMemo(() => {
    const brandSuggestions =
      suggestions[currentSentimentRefinerBrand]?.[marketCode];
    if (!brandSuggestions) return;
    return {
      words: {
        unused: brandSuggestions.unused,
        included: brandSuggestions.included,
        excluded: brandSuggestions.excluded,
      },
      status: brandSuggestions.loaded?.status,
    };
  }, [suggestions, currentSentimentRefinerBrand, marketCode]);

  const suggestionsTableData = useMemo(() => {
    const brandSuggestions =
      suggestions[currentSearchRefinerBrand]?.[marketCode];
    if (!brandSuggestions) return;
    return {
      rows: brandSuggestions.loaded?.data,
      selectedRowIds: brandSuggestions.selected,
      status: brandSuggestions.loaded?.status,
    };
  }, [suggestions, currentSearchRefinerBrand, marketCode]);

  const [currentMentionsBrand, setCurrentMentionsBrand] = useState("");

  const [mentions, updateMentions] = useImmer<Mentions>({});

  const mentionsTableData = useMemo(() => {
    const brandMentions = mentions[currentMentionsBrand]?.[marketCode];
    if (!brandMentions) return;
    return {
      rows: brandMentions.loaded?.data?.filter(
        ({ id }) => !brandMentions?.removed?.[id]
      ),
      status: brandMentions.loaded?.status,
    };
  }, [mentions, currentMentionsBrand, marketCode]);

  const searchesLimit = companySubscriptionPlan?.searchesLimit || 1;

  const searches = useMemo(() => {
    return initialSearches.map((search) => {
      let fullName;
      if (search.owner.id !== userId) {
        fullName = companyMembers.find(({ id }) => id === search.owner.id)
          ?.fullName;
      }
      return {
        ...search,
        owner: { id: search.owner.id, fullName },
      };
    });
  }, [initialSearches, companyMembers, userId]);

  const isFirstMarketsLoading = useRef(true);

  useEffect(() => {
    if (
      isFirstMarketsLoading.current &&
      (marketsStatus === "idle" || marketsStatus === "failed")
    ) {
      isFirstMarketsLoading.current = false;
      dispatch(fetchAllMarkets());
    }
  }, [marketsStatus, dispatch]);

  const isFirstIndustriesLoading = useRef(true);

  useEffect(() => {
    if (
      isFirstIndustriesLoading.current &&
      (industriesStatus === "idle" || industriesStatus === "failed")
    ) {
      isFirstIndustriesLoading.current = false;
      dispatch(fetchAllIndustries());
    }
  }, [industriesStatus, dispatch]);

  function validate() {
    const validationErrors: typeof errors = {};
    const brands = [
      mainBrand,
      competitor1,
      competitor2,
      competitor3,
      competitor4,
    ].map((brand) => brand.trim().toLowerCase());
    const [main, ...competitors] = brands;
    if (!main) validationErrors.mainBrand = "Required";
    if (!competitors[0]) validationErrors.competitor1 = "Required";
    competitors.forEach((brand, i) => {
      if (brand && brands.slice(0, i + 1).includes(brand))
        validationErrors["competitor" + (i + 1)] =
          "This brand is already entered";
    });
    if (!industryId) validationErrors.industryId = "Required";
    if (!marketCode) validationErrors.marketCode = "Required";
    return validationErrors;
  }

  function cleanInputs() {
    setStage(1);
    setMainBrand("");
    setCompetitor1("");
    setCompetitor2("");
    setCompetitor3("");
    setCompetitor4("");
    setIndustryId("");
    setMarketCode("");
  }

  async function onSaveSearchClicked() {
    const filteredCompetitors = [
      competitor1,
      competitor2,
      competitor3,
      competitor4,
    ].filter((x) => x);

    const brands = [mainBrand, ...filteredCompetitors];

    setModalElement(
      <SearchNameModal
        initialSearchName={passedSearch?.name}
        onNextClicked={async (searchName) => {
          setIsGlobalPreloaderShown(true);
          const searchData = {
            name: searchName,
            mainBrand,
            competitors: filteredCompetitors,
            industryId,
            marketCode,
            suggestions: brands.reduce<{
              [brandName: string]: {
                included?: string[];
                excluded?: string[];
                selected?: string[];
              };
            }>((acc, brand) => {
              const brandSuggestions = suggestions[brand]?.[marketCode];

              if (brandSuggestions) {
                acc[brand] = {
                  included: brandSuggestions.included,
                  excluded: brandSuggestions.excluded,
                  selected: Object.keys(brandSuggestions.selected ?? {}),
                };
              }

              return acc;
            }, {}),
          };

          try {
            let searchId;

            if (passedSearch) {
              await editSearch(passedSearch.id, searchData);

              searchId = passedSearch.id;
            } else {
              const createSearchData: any = await createSearch({
                userId,
                ...searchData,
              });

              const returnedSearchId = createSearchData?.searchId;

              if (typeof returnedSearchId === "string" && returnedSearchId) {
                searchId = returnedSearchId;
              }
            }

            if (searchId) {
              try {
                await calculateShareOfSearchWidget(searchId);
              } catch (error) {
                console.dir(error);
              }
            }

            setModalElement(null);

            if (searches.length) {
              cleanInputs();
              notify.show(
                "Your search has now been saved and can be found under saved searches",
                "success",
                5000
              );
              history.push("/searches/new");
            } else {
              history.push("/widget-library");
            }
          } catch (error) {
            console.dir(error);
            notify.show("Oops, something went wrong", "error");
          }
          setIsGlobalPreloaderShown(false);
        }}
      />
    );
  }

  function onDataClicked(id: string) {
    setIsGlobalPreloaderShown(true);
    setTimeout(() => {
      history.push("/searches/" + id + "?stage=2");
      setIsGlobalPreloaderShown(false);
    }, 500);
  }

  const onEditClicked = (id: string) => {
    setIsGlobalPreloaderShown(true);
    setTimeout(() => {
      history.push("/searches/" + id);
      setIsGlobalPreloaderShown(false);
    }, 500);
  };

  async function onDeleteClicked({ id, name }: Store.Search) {
    setModalElement(
      <BasicModal
        redButton={{ onClicked: () => setModalElement(null) }}
        greenButton={{
          onClicked: async () => {
            setIsGlobalPreloaderShown(true);
            try {
              await removeSearch(id);
              setModalElement(null);
              notify.show("Your search has been deleted", "success");
            } catch (error) {
              console.dir(error);
              notify.show("Oops, something went wrong", "error");
            }
            setIsGlobalPreloaderShown(false);
          },
        }}
      >
        Do you really want to delete "{name}" search?
      </BasicModal>
    );
  }

  const onFormSubmitted = () => {
    if (isNewSearch) {
      if (
        searches.filter(({ owner }) => owner.id === userId).length >=
        searchesLimit
      ) {
        setModalElement(
          <BasicModal
            redButton={{ onClicked: () => setModalElement(null) }}
            greenButton={{
              text: "Check plans",
              onClicked: () => {
                setModalElement(null);
                history.push("/pricing");
              },
            }}
          >
            Your current plan has a limit of <b>{searchesLimit}</b> saved search
            {searchesLimit === 1 ? "" : "es"}.
            <br />
            Either you delete your saved search{searchesLimit === 1
              ? ""
              : "es"}{" "}
            or you can upgrade your plan.
          </BasicModal>
        );
        return;
      }
    }

    const errors = validate();
    if (Object.keys(errors).length) {
      setErrors(errors);
      return;
    }

    setMainBrand((x) => x.trim());
    setCompetitor1((x) => x.trim());
    setCompetitor2((x) => x.trim());
    setCompetitor3((x) => x.trim());
    setCompetitor4((x) => x.trim());

    setStage(2);
  };

  const fetchBrandKeywordSuggestions = useCallback(
    async (brand: string) => {
      const status = suggestions[brand]?.[marketCode]?.loaded?.status;
      if (status === "loading" || status === "succeeded" || status === "failed")
        return;

      updateSuggestions((draft) => {
        set(draft, [brand, marketCode, "loaded"], {
          status: "loading",
        });
      });
      try {
        const data = await fetchKeywordSuggestions(brand, marketCode);
        const unusedSuggestions = data.reduce((acc, { withoutBrand }) => {
          if (withoutBrand) acc.push(withoutBrand);
          return acc;
        }, [] as string[]);
        updateSuggestions((draft) => {
          update(draft, [brand, marketCode], (brandSuggestions) => {
            return {
              ...brandSuggestions,
              loaded: {
                status: "succeeded",
                data,
              },
              unused: unusedSuggestions,
            };
          });
        });
      } catch (error) {
        console.dir(error);
        updateSuggestions((draft) => {
          set(draft, [brand, marketCode, "loaded"], {
            status: "failed",
          });
        });
      }
    },
    [suggestions, marketCode, updateSuggestions]
  );

  useEffect(() => {
    if (!(stage === 2)) return;

    const brandsToFetch = new Set<string>();

    const nextSentimentRefinerBrand = filledInBrands.includes(
      currentSentimentRefinerBrand
    )
      ? currentSentimentRefinerBrand
      : filledInBrands[0]!;
    setCurrentSentimentRefinerBrand(nextSentimentRefinerBrand);
    brandsToFetch.add(nextSentimentRefinerBrand);

    const nextSearchRefinerBrand = filledInBrands.includes(
      currentSearchRefinerBrand
    )
      ? currentSearchRefinerBrand
      : filledInBrands[0]!;
    setCurrentSearchRefinerBrand(nextSearchRefinerBrand);
    brandsToFetch.add(nextSearchRefinerBrand);

    brandsToFetch.forEach((brand) => {
      fetchBrandKeywordSuggestions(brand);
    });
  }, [
    stage,
    currentSentimentRefinerBrand,
    currentSearchRefinerBrand,
    filledInBrands,
    fetchBrandKeywordSuggestions,
  ]);

  const fetchBrandMentions = useCallback(
    async (brand: string) => {
      const status = mentions[brand]?.[marketCode]?.loaded?.status;
      if (status === "loading" || status === "succeeded" || status === "failed")
        return;

      updateMentions((draft) => {
        set(draft, [brand, marketCode, "loaded"], {
          status: "loading",
        });
      });
      try {
        const data = await fetchMentions(brand, marketCode);
        updateMentions((draft) => {
          set(draft, [brand, marketCode, "loaded"], {
            status: "succeeded",
            data,
          });
        });
      } catch (error) {
        console.dir(error);
        updateMentions((draft) => {
          set(draft, [brand, marketCode, "loaded"], {
            status: "failed",
          });
        });
      }
    },
    [mentions, marketCode, updateMentions]
  );

  useEffect(() => {
    if (!(stage === 2)) return;

    const nextMentionsBrand = filledInBrands.includes(currentMentionsBrand)
      ? currentMentionsBrand
      : filledInBrands[0]!;
    setCurrentMentionsBrand(nextMentionsBrand);
    fetchBrandMentions(nextMentionsBrand);
  }, [stage, filledInBrands, currentMentionsBrand, fetchBrandMentions]);

  if (
    marketsStatus === "idle" ||
    marketsStatus === "loading" ||
    industriesStatus === "idle" ||
    industriesStatus === "loading"
  ) {
    return <Preloader modifier="inner" />;
  }

  const BrandSelector = (
    currentBrand: string,
    onBrandClicked: (brand: string) => void
  ) => {
    return (
      <div className={styles.brandSelector}>
        {filledInBrands.map((brand) => (
          <button
            className={cx(styles.brandButton, {
              [styles.brandButtonActive!]: brand === currentBrand,
            })}
            onClick={() => onBrandClicked(brand)}
            key={brand}
          >
            {brand}
          </button>
        ))}
      </div>
    );
  };

  return (
    <div className={styles.search}>
      {stage === 1 && (
        <div className={styles.stage1}>
          <form
            className={styles.form}
            onSubmit={(e) => {
              e.preventDefault();
              onFormSubmitted();
            }}
          >
            <div className={styles.title}>
              {passedSearch
                ? "EDIT SEARCH - " + passedSearch.name
                : "NEW SEARCH"}
            </div>
            <div className={styles.inputGroups}>
              <div className={styles.inputGroup}>
                <div className={styles.inputWrapper}>
                  <Label leftText="Main brand or topic to track" />
                  <InputWithError
                    className={styles.input}
                    value={mainBrand}
                    changeHandler={(value) => setMainBrand(value)}
                    error={errors.mainBrand}
                  />
                </div>
              </div>
              <div className={styles.inputGroup}>
                <div className={styles.inputWrapper}>
                  <Label leftText="Compare with 1" />
                  <InputWithError
                    className={styles.input}
                    value={competitor1}
                    changeHandler={(value) => setCompetitor1(value)}
                    error={errors.competitor1}
                  />
                </div>
                <div className={styles.inputWrapper}>
                  <Label leftText="Compare with 2" rightText="(Optional)" />
                  <InputWithError
                    className={styles.input}
                    value={competitor2}
                    changeHandler={(value) => setCompetitor2(value)}
                    error={errors.competitor2}
                  />
                </div>
                <div className={styles.inputWrapper}>
                  <Label leftText="Compare with 3" rightText="(Optional)" />
                  <InputWithError
                    className={styles.input}
                    value={competitor3}
                    changeHandler={(value) => setCompetitor3(value)}
                    error={errors.competitor3}
                  />
                </div>
                <div className={styles.inputWrapper}>
                  <Label leftText="Compare with 4" rightText="(Optional)" />
                  <InputWithError
                    className={styles.input}
                    value={competitor4}
                    changeHandler={(value) => setCompetitor4(value)}
                    error={errors.competitor4}
                  />
                </div>
              </div>
              <div className={styles.inputGroup}>
                <div className={styles.inputWrapper}>
                  <Label leftText="Market" />
                  <SelectWithError
                    options={marketOptions}
                    value={marketCode}
                    changeHandler={(value) => setMarketCode(value)}
                    error={errors.marketCode}
                  />
                </div>
                <div className={styles.inputWrapper}>
                  <Label leftText="Industry" />
                  <SelectWithError
                    options={industryOptions}
                    value={industryId}
                    changeHandler={(value) => setIndustryId(value)}
                    error={errors.industryId}
                  />
                </div>
              </div>
            </div>
            <button
              ref={nextButtonRef}
              className={styles.nextButton}
              style={{ marginTop: 32 }}
              type="submit"
            >
              Next
            </button>
          </form>
          <div className={styles.list}>
            <div className={styles.title}>YOUR SAVED SEARCHES</div>
            <div className={styles.listItems}>
              {searches.map((search, i) => (
                <div className={styles.listItem} key={i}>
                  <div className={styles.listItemText}>
                    <div title={search.name}>
                      {search.name}
                      {!!search.owner.fullName &&
                        ` (by ${search.owner.fullName})`}
                    </div>
                    {search.isPending && <div>PENDING</div>}
                  </div>
                  {search.owner.id === userId && (
                    <>
                      {search.id !== passedSearch?.id && (
                        <>
                          <div
                            className={styles.listItemButtonData}
                            onClick={() => onDataClicked(search.id)}
                          >
                            <div>Data</div>
                          </div>
                          <div
                            className={styles.listItemButtonEdit}
                            onClick={() => onEditClicked(search.id)}
                          >
                            <div>Edit</div> <Edit width={15} />
                          </div>
                        </>
                      )}
                      <div
                        className={styles.listItemButtonDelete}
                        onClick={() => onDeleteClicked(search)}
                      >
                        <div>Delete</div> <Trash size={18} />
                      </div>
                    </>
                  )}
                </div>
              ))}
              {searches.length < 3 &&
                [...Array(3 - searches.length).keys()].map((_, i) => (
                  <div className={styles.listItem} key={i}></div>
                ))}
            </div>
          </div>
        </div>
      )}
      {stage === 2 && (
        <div className={styles.stage2}>
          <div className={styles.title}>
            {passedSearch ? "EDIT SEARCH - " + passedSearch.name : "NEW SEARCH"}
          </div>
          <div>
            <button className={styles.backButton} onClick={() => setStage(1)}>
              Go back
            </button>
            <div style={{ marginTop: 16 }}>
              <span className={styles.subtitle}>
                At this point, you could either start off with the search you
                just did or you could tweak your data to make sure it suits you
              </span>
              <button
                className={styles.nextButton}
                style={{ margin: "4px 4px 4px 16px" }}
                type="button"
                onClick={() => onSaveSearchClicked()}
              >
                Next
              </button>
            </div>
          </div>
          <div className={styles.section}>
            <div className={styles.aside}>
              <div className={styles.subtitleWrapper}>
                <div className={styles.subtitle}>
                  Refine your sentiment data
                </div>
                <Warning className={styles.warningIcon} />
              </div>
              <div className={styles.description}>
                <p>
                  <b>
                    Your search is now taking in all references of your entered
                    brand name, if needed you can narrow down your search by
                    only looking at certain words or remove words not related to
                    your brand.
                  </b>
                </p>
                <p>
                  <span className={styles.warningText}>(Warning)</span> As this
                  can skew your data only do this for generic brand names that
                  are shared with other unrelated topics/brands such as Apple
                  or if you wish to only include specific topics regarding a
                  brand such as Apple AND headphones. Otherwise we recommend
                  you to not add words.
                </p>
                <p
                  className={styles.toggle}
                  onClick={() =>
                    setIsSentimentSourcesDropdownOpen((prev) => !prev)
                  }
                >
                  <Triangle
                    className={styles.toggleIcon}
                    style={{
                      transform: `rotate(${
                        isSentimentSourcesDropdownOpen ? 180 : 90
                      }deg)`,
                    }}
                  />
                  <b>Sentiment data collected from:</b>
                </p>
                {isSentimentSourcesDropdownOpen && (
                  <>
                    <p>
                      Online news (Financial Times, Dagens Nyheter, LeMonde,
                      etc)
                    </p>
                    <p>Social media (Facebook, Twitter, Instagram, etc)</p>
                    <p>Blogs (Yanko Design, kitchn, etc)</p>
                    <p>Forums (Reddit, etc)</p>
                    <p>
                      Product reviews (Yelp, etc), video* (Youtube, Vimeo, etc.)
                    </p>
                  </>
                )}
              </div>
            </div>
            <div className={styles.main}>
              {BrandSelector(
                currentSentimentRefinerBrand,
                setCurrentSentimentRefinerBrand
              )}
              <WordSelector
                className={styles.wordSelector}
                {...wordSelectorData}
                onWordStatusChanged={(words) => {
                  updateSuggestions((draft) => {
                    update(
                      draft,
                      [currentSentimentRefinerBrand, marketCode],
                      (brandSuggestions) => ({
                        ...brandSuggestions,
                        ...words,
                      })
                    );
                  });
                }}
              />
            </div>
          </div>
          <div className={styles.section}>
            <div className={styles.aside}>
              <div className={styles.subtitleWrapper}>
                <div className={styles.subtitle}>Refine your search data</div>
                <Warning className={styles.warningIcon} />
              </div>
              <div className={styles.description}>
                <p>
                  <b>
                    Your search is now taking in all data related to your
                    entered brand name, if needed you can narrow down your
                    search by only looking at certain words or remove words
                    completely unrelated to your brand.
                  </b>
                </p>
                <p>
                  <span className={styles.warningText}>(Warning)</span> As this
                  can skew your data only do this for generic brand names that
                  are shared with other unrelated topics/brands such as Apple
                  or if you wish to only include specific search terms of a
                  brand such as Apple iPhone. Otherwise we recommend you to
                  not add words.
                </p>

                <p
                  className={styles.toggle}
                  onClick={() =>
                    setIsSearchSourcesDropdownOpen((prev) => !prev)
                  }
                >
                  <Triangle
                    className={styles.toggleIcon}
                    style={{
                      transform: `rotate(${
                        isSearchSourcesDropdownOpen ? 180 : 90
                      }deg)`,
                    }}
                  />
                  <b>Search data collected from:</b>
                </p>
                {isSearchSourcesDropdownOpen && (
                  <>
                    <p>Google</p>
                    <p>Youtube</p>
                    <p>Bing</p>
                    <p>Amazon (USA)</p>
                    <p>eBay</p>
                    <p>Play Store</p>
                    <p>Instagram (Global Searches)</p>
                    <p>Twitter (Global Searches)</p>
                  </>
                )}
              </div>
            </div>
            <div className={styles.main}>
              {BrandSelector(
                currentSearchRefinerBrand,
                setCurrentSearchRefinerBrand
              )}
              <SearchTable
                className={styles.searchTable}
                columns={[
                  { label: "Keywords", field: "id" },
                  {
                    label: "Search volume",
                    field: "volume",
                    isSortable: true,
                    isSqueezed: true,
                  },
                ]}
                {...suggestionsTableData}
                hasFilter
                filteringFields={["id", "volume"]}
                hasRowSelection
                onSelectedRowIdsChanged={(selectedRowIds) => {
                  updateSuggestions((draft) => {
                    set(
                      draft,
                      [currentSearchRefinerBrand, marketCode, "selected"],
                      selectedRowIds
                    );
                  });
                }}
              />
            </div>
          </div>
          <div className={styles.section}>
            <div className={styles.aside}>
              <div className={styles.subtitle}>Mentions</div>
              <div className={styles.description}>
                <p>
                  These are your up to 200 latest mentions of your brand. This
                  is for you to get an understanding of your data and remove all
                  topics that are not relevant for your brand analysis. It will
                  take up to 72 hours for your news mentions to show up, so feel
                  free to return to this part after your search is done to see
                  your results.
                </p>
              </div>
            </div>
            <div className={styles.main}>
              {BrandSelector(currentMentionsBrand, setCurrentMentionsBrand)}
              <SearchTable
                className={styles.searchTable}
                columns={[
                  { label: "Article", field: "link", isExpandable: true },
                  {
                    label: "Source",
                    field: "source",
                    isSortable: true,
                    isSqueezed: true,
                  },
                  {
                    label: "Category",
                    field: "category",
                    isSortable: true,
                    isSqueezed: true,
                  },
                  {
                    label: "Date",
                    field: "time",
                    isSortable: true,
                    isDate: true,
                    isSqueezed: true,
                  },
                ]}
                {...mentionsTableData}
                hasRowDeletion
                onRowDeleted={(id) => {
                  updateMentions((draft) => {
                    update(
                      draft,
                      [currentMentionsBrand, marketCode, "removed"],
                      (removed) => ({ ...removed, [id]: true })
                    );
                  });
                }}
                noRowsText="No mentions found"
              />
              <button
                className={styles.nextButton}
                style={{ marginTop: 32 }}
                onClick={() => onSaveSearchClicked()}
              >
                Next
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

const mapStateToProps = ({ user, company, searches }: Store.State) => ({
  userId: user.id,
  companyMembers: company.members,
  searches,
});

export default connect(mapStateToProps, actions)(Search);
