import { CSSProperties, FC, useEffect, useMemo, useState } from "react";
import styles from "./SearchTable.module.scss";
import { Input, Preloader } from "../../components";
import {
  Triangle,
  Trash,
  Magnifier,
  CheckBoxChecked,
  CheckBoxEmpty,
  ChevronLeft,
  ChevronRight,
} from "../../assets/icons";
import Pagination from "react-paginate";
import cx from "classnames";
import omit from "lodash/omit";
import formatDistanceToNow from "date-fns/formatDistanceToNow";

type Sorting = {
  field: string;
  direction: "asc" | "desc";
} | null;

type SelectedRowIds = {
  [id: string]: true;
};

interface Props {
  className?: string;
  style?: CSSProperties;
  status?: "loading" | "succeeded" | "failed";
  columns: {
    label: string;
    field: string;
    isSortable?: boolean;
    isDate?: boolean;
    isSqueezed?: boolean;
    isExpandable?: boolean;
  }[];
  rows?: {
    id: string;
    [field: string]: string | number | undefined;
  }[];
  hasFilter?: boolean;
  filteringFields?: string[];
  hasRowSelection?: boolean;
  selectedRowIds?: SelectedRowIds;
  onSelectedRowIdsChanged?: (selectedRowIds: SelectedRowIds) => void;
  hasRowDeletion?: boolean;
  onRowDeleted?: (rowId: string) => void;
  noRowsText?: string;
}

export const SearchTable: FC<Props> = ({
  className,
  style,
  status,
  columns,
  rows = [],
  hasFilter,
  filteringFields,
  hasRowSelection,
  selectedRowIds = {},
  onSelectedRowIdsChanged,
  hasRowDeletion,
  onRowDeleted,
  noRowsText = "No suggestions found",
}) => {
  // FILTERING
  const [filter, setFilter] = useState("");

  const filteredRows = useMemo(() => {
    if (!filteringFields?.length) return rows;

    const filterInLowerCase = filter.toLowerCase();

    return rows.filter((row) => {
      const joinedValues = filteringFields.reduce((acc, field) => {
        const value = row[field] ? String(row[field]).toLowerCase() : "";
        return acc + value;
      }, "");

      return joinedValues.includes(filterInLowerCase);
    });
  }, [filteringFields, filter, rows]);

  // SORTING
  const [sorting, setSorting] = useState<Sorting>(null);

  const sortedRows = useMemo(() => {
    if (!sorting) return filteredRows;

    const { field, direction } = sorting;

    return [...filteredRows].sort((a, b) => {
      let valueA = a[field];
      let valueB = b[field];

      if (typeof valueA === "string" && typeof valueB === "string") {
        valueA = valueA.toLowerCase();
        valueB = valueB.toLowerCase();
        return direction === "desc"
          ? valueB.localeCompare(valueA)
          : valueA.localeCompare(valueB);
      }

      if (typeof valueA === "number" && typeof valueB === "number") {
        if (isNaN(valueA)) valueA = 0;
        if (isNaN(valueB)) valueB = 0;
        return direction === "desc" ? valueB - valueA : valueA - valueB;
      }

      return 0;
    });
  }, [sorting, filteredRows]);

  function onSortableFieldClicked(field: string) {
    const previousField = sorting?.field;

    if (field === previousField) {
      const previousDirection = sorting?.direction;
      setSorting(
        previousDirection === "desc"
          ? {
              field,
              direction: "asc",
            }
          : null
      );
    } else {
      setSorting({
        field,
        direction: "desc",
      });
    }
  }

  // PAGINATION
  const [page, setPage] = useState(0);

  const pageQuantity = useMemo(() => Math.ceil(filteredRows.length / 10), [
    filteredRows,
  ]);

  const pageRows = useMemo(() => sortedRows.slice(10 * page, 10 * page + 10), [
    sortedRows,
    page,
  ]);

  useEffect(() => {
    if (page >= pageQuantity) setPage(0);
  }, [page, pageQuantity]);

  // SELECTION
  const isEachPageRowSelected = useMemo(() => {
    return !!pageRows.length && pageRows.every(({ id }) => selectedRowIds[id]);
  }, [pageRows, selectedRowIds]);

  function onBlankMultiCheckboxClicked() {
    if (!(onSelectedRowIdsChanged && pageRows.length)) return;

    const newSelectedRowIds = { ...selectedRowIds };
    pageRows.forEach(({ id }) => (newSelectedRowIds[id] = true));

    onSelectedRowIdsChanged(newSelectedRowIds);
  }

  function onMultiCheckboxClicked() {
    onSelectedRowIdsChanged?.call(
      null,
      omit(
        selectedRowIds,
        pageRows.map(({ id }) => id)
      )
    );
  }

  function onRemoveClicked(id: string) {
    onRowDeleted?.call(null, id);
  }

  function onBlankCheckboxClicked(id: string) {
    onSelectedRowIdsChanged?.call(null, { ...selectedRowIds, [id]: true });
  }

  function onCheckboxClicked(id: string) {
    onSelectedRowIdsChanged?.call(null, omit(selectedRowIds, [id]));
  }

  // MISC
  function toHTML(value: unknown) {
    switch (typeof value) {
      case "string":
        return value;
      case "number":
        return value.toLocaleString();
      default:
        return "";
    }
  }

  function toDateDistance(value: unknown) {
    if (typeof value !== "number" || isNaN(value)) return "";
    return formatDistanceToNow(value, {
      addSuffix: true,
    });
  }

  return (
    <div className={className} style={style}>
      {hasFilter && (
        <div className={styles.filter}>
          <Input
            className={styles.input}
            value={filter}
            changeHandler={(value) => setFilter(value)}
            placeholder="Select the search terms connected to your brand"
            maxLength={60}
          />
          <Magnifier
            className={styles.filterIcon}
            size={20}
            opacity={filter ? 1 : 0.25}
          />
        </div>
      )}
      <div className={styles.main}>
        {status === "loading" && <Preloader className={styles.preloader} />}
        {status === "succeeded" &&
          (rows.length ? (
            <>
              <div className={styles.tableWrapper}>
                <table>
                  <thead>
                    <tr>
                      {hasRowDeletion && <th style={{ width: 1 }} />}
                      {hasRowSelection && (
                        <th style={{ width: 1, fontSize: 0 }}>
                          {isEachPageRowSelected ? (
                            <CheckBoxChecked
                              className={styles.checkboxIcon}
                              color="#41b167"
                              size={16}
                              onClick={onMultiCheckboxClicked}
                            />
                          ) : (
                            <CheckBoxEmpty
                              className={styles.checkboxIcon}
                              size={16}
                              onClick={onBlankMultiCheckboxClicked}
                            />
                          )}
                        </th>
                      )}
                      {columns.map(
                        ({ label, field, isSortable, isSqueezed }, i) => (
                          <th
                            style={{
                              width: isSqueezed ? 1 : undefined,
                              cursor: isSortable ? "pointer" : "auto",
                            }}
                            onClick={
                              isSortable
                                ? () => onSortableFieldClicked(field)
                                : undefined
                            }
                            key={i}
                          >
                            {sorting?.field === field && (
                              <Triangle
                                className={cx(styles.sortingIcon, {
                                  [styles.sortingIconDown!]:
                                    sorting?.direction === "desc",
                                })}
                                size={8}
                              />
                            )}
                            {label}
                          </th>
                        )
                      )}
                    </tr>
                  </thead>
                  <tbody>
                    {pageRows.map((fields, i) => (
                      <tr key={i}>
                        <>
                          {hasRowDeletion && (
                            <td>
                              <Trash
                                className={styles.trashIcon}
                                size={16}
                                onClick={() => onRemoveClicked(fields.id)}
                              />
                            </td>
                          )}
                          {hasRowSelection && (
                            <td style={{ fontSize: 0 }}>
                              {selectedRowIds[fields.id] ? (
                                <CheckBoxChecked
                                  className={styles.checkboxIcon}
                                  color="#41b167"
                                  size={16}
                                  onClick={() => onCheckboxClicked(fields.id)}
                                />
                              ) : (
                                <CheckBoxEmpty
                                  className={styles.checkboxIcon}
                                  size={16}
                                  onClick={() =>
                                    onBlankCheckboxClicked(fields.id)
                                  }
                                />
                              )}
                            </td>
                          )}
                          {columns.map(({ field, isDate, isExpandable }, k) => (
                            <td
                              className={cx({
                                [styles.expandableCell!]: isExpandable,
                              })}
                              dangerouslySetInnerHTML={{
                                __html: isDate
                                  ? toDateDistance(fields[field])
                                  : toHTML(fields[field]),
                              }}
                              key={k}
                            />
                          ))}
                        </>
                      </tr>
                    ))}
                  </tbody>
                </table>
                {!filteredRows.length && (
                  <div className={styles.warning}>
                    No entries are matching your search criteria
                  </div>
                )}
              </div>
              <div className={styles.paginationWrapper}>
                {!!pageQuantity && (
                  <Pagination
                    previousLabel={<ChevronLeft />}
                    nextLabel={<ChevronRight />}
                    breakLabel={"..."}
                    pageCount={pageQuantity}
                    marginPagesDisplayed={1}
                    pageRangeDisplayed={2}
                    forcePage={page}
                    onPageChange={({ selected }) => setPage(selected)}
                    containerClassName={styles.pagination}
                    activeLinkClassName={styles.activeLink}
                  />
                )}
              </div>
            </>
          ) : (
            <div className={styles.message}>{noRowsText}</div>
          ))}
        {status === "failed" && (
          <div className={styles.message}>Failed to fetch data :(</div>
        )}
      </div>
    </div>
  );
};
