import { FC, useState } from "react";
import styles from "./SearchNameModal.module.scss";
import { Input } from "../../components";
import { useTemporaryErrors } from "../../hooks";
import { withError } from "../../hocs";

const InputWithError = withError(Input);

interface Props {
  initialSearchName?: string;
  onNextClicked: (searchName: string) => void;
}

export const SearchNameModal: FC<Props> = (props) => {
  const [searchName, setSearchName] = useState(props.initialSearchName ?? "");

  const { errors, setErrors } = useTemporaryErrors(3000);

  function onNextClicked() {
    const trimmedSearchName = searchName.trim();

    if (!trimmedSearchName) {
      setErrors({ searchName: "Required" });
      return;
    }

    props.onNextClicked(trimmedSearchName);
  }

  return (
    <div className={styles.searchNameModal}>
      <div className={styles.label}>Save this search as:</div>
      <div className={styles.inputWrapper}>
        <InputWithError
          className={styles.input}
          value={searchName}
          changeHandler={(value) => setSearchName(value)}
          error={errors.searchName}
        />
      </div>
      <button className={styles.greenButton} onClick={onNextClicked}>
        Save
      </button>
    </div>
  );
};
